@import url(https://fonts.googleapis.com/css?family=Montserrat);
$xs-screen: 575px;
$sm-screen: 768px;
$md-screen: 1000px;
$lg-screen: 1110px;

@mixin xs-mobile {
  @media (max-width: #{$xs-screen - 0.1px}) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: #{$sm-screen - 0.1px}) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: #{$md-screen - 0.1px}) {
    @content;
  }
}

@mixin laptop {
  @media (max-width: #{$lg-screen - 0.01px}) {
    @content;
  }
}
* {
    box-sizing: border-box;

}

html {
    font-size: 18px;
}

body,
ul,
ol,
h1,
h2,
h3,
h4,
p {
    margin: 0;
    padding: 0;
}

ul,
ol {
    list-style: none;
}

a {
    text-decoration: none;
}

input,
button {
    outline: none;
}
body {
  background-color: #1b1b1b;
  overflow: hidden;
  height: 100vh; }

.app {
  font-family: 'Montserrat', sans-serif;
  color: springgreen;
  position: absolute;
  left: 50%;
  height: 100vh;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 50%;
  min-height: 680px; }
  @media (max-width: 999.9px) {
    .app {
      width: 60%; } }
  @media (max-width: 767.9px) {
    .app {
      width: 80%; } }
  @media (max-width: 574.9px) {
    .app {
      width: 90%;
      min-height: 500px; } }
  .app__title {
    padding-top: 3%;
    letter-spacing: 0.8px;
    font-size: 38px;
    padding-bottom: 3%; }
    @media (max-width: 1109.99px) {
      .app__title {
        font-size: 34px; } }
    @media (max-width: 999.9px) {
      .app__title {
        font-size: 30px; } }
    @media (max-width: 767.9px) {
      .app__title {
        font-size: 20px; } }
    @media (max-width: 574.9px) {
      .app__title {
        font-size: 18px; } }
  .app__buttons {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    height: 130px; }
  .app__row-buttons {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap; }
  .app__button {
    width: 24.5%;
    border: solid 2px springgreen;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    margin-bottom: 5px;
    padding: 5px 0px;
    cursor: pointer;
    font-size: 18px; }
    @media (max-width: 1109.99px) {
      .app__button {
        width: 30%;
        font-size: 16px; } }
    @media (max-width: 999.9px) {
      .app__button {
        font-size: 13px;
        width: 24%; } }
    @media (max-width: 767.9px) {
      .app__button {
        font-size: 11px;
        width: 24%; } }
    @media (max-width: 574.9px) {
      .app__button {
        width: 24%; } }
  .app__button:hover {
    background-color: white; }
  .app__blocks {
    display: flex;
    width: 100%;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-top: 3%; }
  .app__block {
    opacity: 1;
    width: 23.5%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 3% 0%;
    border: solid 2px springgreen;
    border-radius: 100px;
    margin: 3px 3px 5px 3px;
    cursor: pointer;
    -webkit-transition: 1.5s;
    transition: 1.5s; }
    @media (max-width: 1109.99px) {
      .app__block {
        width: 23%; } }
    @media (max-width: 999.9px) {
      .app__block {
        width: 23%; } }
    @media (max-width: 574.9px) {
      .app__block {
        padding: 3% 0%; } }
  .app__block:hover {
    background-color: white; }
  .app__icon {
    font-size: 80px; }
    @media (max-width: 1109.99px) {
      .app__icon {
        width: 80px; } }
    @media (max-width: 767.9px) {
      .app__icon {
        font-size: 66px; } }
    @media (max-width: 574.9px) {
      .app__icon {
        font-size: 63px; } }

